body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height:100%;
}

html {
  padding:0px;
  margin:0px;
  height:100%;
}
#root{
  height:100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.appContainer{
  padding:0px;
  margin:0px;
  height:100%;
  padding-top: 64px;
  box-sizing:border-box;
}

.searchHolder{
  display:flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  width: 350px;
  padding: 5px 10px 5px 10px;
  margin-left:auto;
  background-color: #378e81;
  box-sizing:border-box;
}

.searchHolder input{
  width:290px;
  color:#ffffff; 
  padding-left:10px;
}

.searchHolder .searchIcon{
  margin-top:2px;
}

.contentContainer{
  display:flex;
  flex-direction: column;
  flex-grow: 1;  /*ensures that the container will take up the full height of the parent container*/
  overflow-y: auto;  /*adds scroll to this container*/
}

.fullHeight{
  height:100%;
}

.rotate-90 {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.modalBoxMultipleSelect{
  margin-top:20px;
}

.loginContainer{
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position:fixed;
    height:100%;
    width:100%;
    background-color:#fafafa;
    top:0px;
    left:0px;
}

.login{
    width:400px;
    height:375px;
    border: 1px solid #eeeeee;
    padding: 30px;
    box-sizing:border-box;
    background-color:#ffffff;
    display:flex;
    align-items:center;
    flex-direction: column;
    box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
}

.loginInput{
    width:100%;
    margin-top:10px !important;
}

.loginButton{
    width:100%;
    margin-top:70px !important;
    height:40px;
}

.loginRememberMe{
    width:100%
}

.loginRememberMeLabel span{
    font-size:14px !important;
}
.sidebar{
    height: 100%; 
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    order: -1;
    background-color:#ffffff;
    border-right:2px solid #DFDCE3; 
    width:300px;  
  }

.sidebar .collapsePadding{
    padding-left:15px;
    box-sizing:border-box
}

.sidebarClosed{
    background-color:#378e81 !important;
}

.sidebarSubheader{
    display:flex;
    padding:5px !important;
    justify-content: space-between;
}

.sidebarSubheader svg:hover{
    cursor:pointer;
}
.contentContainer{
    padding:20px;
    background-color:#eeeeee;
}

.width-300{
    width:300px !important;
}

.noPadding{
    padding:0px !important;
}
